<template>
	<view>
		<view class="box">
			<view class="cu-bar search bg-white">
				<view class="search-form round">
					<text class="cuIcon-search"></text>
					<input v-model="xiaoqu_guanjianzi" type="text" placeholder="搜索小区的名称" confirm-type="search" />
				</view>
				<view @click="chaxun_xiaoqu()" class="action"><button class="cu-btn bg-blue shadow-blur round">搜索</button></view>
			</view>
		</view>
		
		<view class="cu-card article " style="margin:1rpx">
			<view @click="gengxin_moren_xiaoqu([item.quartersId])" v-for="(item,index) in xiaoqu_liebiao" :key="index" class="cu-item shadow">
				<view class="title" >
					<view class="text-cut">{{item.quartersName}}</view>
				</view>
				<view class="content" >
					<image :src= "item.bannerUrl" mode="aspectFill"></image>
					<view class="desc">
						<view class="text-content">
							<view>{{item.areaName}}</view>
							<view style="font-size: 90%;">{{item.quartersAddr}}</view>
						</view>	
						<view>
							<view class="cu-tag bg-green light sm round">距离：{{item.distanceUm}}M</view>
							<view class="cu-tag bg-red light sm round">设为默认</view>
						</view>
					</view> 
				</view>
			</view>
		</view>
		
		
	</view>
</template>

<script>
	export default {
		data() {
			return {
				tupian:getApp().globalData.public_server_img +"img/xq_1.png",
				xiaoqu_guanjianzi:"莲花",
				xiaoqu_liebiao:[],
				
				
			}
		},
		methods: {
			chaxun_xiaoqu:function(){
				let _this  = this
				_this.xiaoqu_liebiao  = []
				
				if(!_this.xiaoqu_guanjianzi){
						uni.showModal({
							title: '提示',
							content: "小区名称不能为空",
							showCancel: false,
							success: function() {}
						});
						return false;
				}
				
				let data = {
					quartersName:_this.xiaoqu_guanjianzi,
					longitude:getApp().globalData.longitude,
					dimension:getApp().globalData.latitude,
				}
				_this.wangluo_qingqiu('/wx/ipms/selectquarters', data, true, true, 'GET').then(value => {
					if (value) {
						if(value.length == 0){
							uni.showModal({
								title: '提示',
								content: "没有搜索到合适的小区",
								showCancel: false,
								success: function() {}
							});
						}
						_this.xiaoqu_liebiao = value
					}
				})
			}
			,gengxin_moren_xiaoqu:function(res){
				
				let xiaoqu_id =  res[0]
				let _this  = this
				
				let data = {
					quartersId:xiaoqu_id,
				}
				_this.wangluo_qingqiu('/wx/ipms/modifydefaultquarters', data, true, true, 'POST').then(value => {
					if (value) {
							uni.showModal({
								title: '提示',
								content: "修改默认小区完成",
								showCancel: false,
								success: function() {
									getApp().globalData.public_user_property_id = xiaoqu_id
									uni.navigateBack({
										
									})
								}
							});
					}
				})
				
				
				
			}
		}
	}
</script>

